import { withHistory } from "../../../../../common/util/withHistory";
import { mapFromApiZone, zoneWithinImage } from "../../helpers";
import { IApiZone } from "../../types";
import { CurrentAction, PageSaveState, State } from "../state";

export interface ResetZonesAction {
  page: number;
  type: "RESET ZONES";
  zones: Array<IApiZone>;
}

export const resetZones = (state: State, action: ResetZonesAction): State => {
  const zones = action.zones
    .map(mapFromApiZone(state.pages[action.page].dimensions))
    .filter(zoneWithinImage(state.pages[action.page].dimensions));
  return {
    ...state,
    pages: [
      ...state.pages.slice(0, action.page),
      {
        ...state.pages[action.page],
        activeZones: [],
        currentAction: CurrentAction.HOVERING_NOTHING,
        firstDragEvent: false,
        hoveredZone: -1,
        index: action.page,
        pageSaveState: PageSaveState.Dirty,
        previousMouseLocation: null,
        selection: null,
        zoneHistory: withHistory(zones),
        zones,
      },
      ...state.pages.slice(action.page + 1),
    ],
  };
};
