import { doZonesOverlap, isPointInZone, isZoneInSelection } from "../../helpers";
import { Point, Size } from "../../Renderer/types";
import { IZone } from "../../types";
import { CurrentAction, PageSaveState, PageState, State } from "../state";

export interface MouseMoveAction {
  imageSize: Size;
  location: Point;
  type: "MOUSE MOVE";
}

const creatingOrSelectingZones = (state: PageState, action: MouseMoveAction): PageState => {
  if (!state.selection) {
    if (state.firstDragEvent) {
      return { ...state, firstDragEvent: false };
    }
    return state;
  }
  const newSelection = {
    ...state.selection,
    endx: action.location.x,
    endy: action.location.y,
  };
  const intersectingZones: Array<number> = [];
  for (let i = 0; i < state.zones.length; i++) {
    if (isZoneInSelection(newSelection)(state.zones[i])) {
      intersectingZones.push(i);
    }
  }
  return {
    ...state,
    activeZones: intersectingZones,
    currentAction:
      intersectingZones.length > 0 ? CurrentAction.SELECTING : CurrentAction.CREATING_ZONE,
    firstDragEvent: false,
    previousMouseLocation: action.location,
    selection: newSelection,
  };
};

const movingZones = (state: PageState, action: MouseMoveAction): PageState => {
  if (state.previousMouseLocation && state.activeZones.length > 0) {
    const diff = {
      x: action.location.x - state.previousMouseLocation.x,
      y: action.location.y - state.previousMouseLocation.y,
    };
    const staticZones: Array<IZone> = [];
    const updatedZones = state.zones.map((zone, index) => {
      if (state.activeZones.indexOf(index) < 0) {
        staticZones.push(zone);
        return zone;
      }
      return {
        ...zone,
        maxX: zone.maxX + diff.x,
        maxY: zone.maxY + diff.y,
        minX: zone.minX + diff.x,
        minY: zone.minY + diff.y,
      };
    });
    for (const activeIndex of state.activeZones) {
      const activeZone = updatedZones[activeIndex];
      if (
        activeZone.minX < 0 ||
        activeZone.minY < 0 ||
        activeZone.maxX > action.imageSize.width ||
        activeZone.maxY > action.imageSize.height
      ) {
        return {
          ...state,
          firstDragEvent: false,
          previousMouseLocation: action.location,
        };
      }
      const intersection = !!staticZones.find(doZonesOverlap(activeZone));
      if (intersection) {
        return {
          ...state,
          firstDragEvent: false,
          previousMouseLocation: action.location,
        };
      }
    }
    return {
      ...state,
      firstDragEvent: false,
      pageSaveState: PageSaveState.Dirty,
      previousMouseLocation: action.location,
      zones: state.firstDragEvent
        ? state.zoneHistory.push(updatedZones)
        : state.zoneHistory.replace(updatedZones),
    };
  }
  return {
    ...state,
    firstDragEvent: false,
    previousMouseLocation: action.location,
  };
};

const hoveringNothing = (state: PageState, action: MouseMoveAction): PageState => {
  const intersectingZoneIndex = state.zones.findIndex(isPointInZone(action.location));
  if (intersectingZoneIndex >= 0) {
    return {
      ...state,
      currentAction: CurrentAction.HOVERING_ZONE,
      firstDragEvent: false,
      hoveredZone: intersectingZoneIndex,
    };
  }
  return state;
};

const hoveringZone = (state: PageState, action: MouseMoveAction): PageState => {
  const intersectingZoneIndex = state.zones.findIndex(isPointInZone(action.location));
  if (intersectingZoneIndex < 0) {
    return {
      ...state,
      currentAction: CurrentAction.HOVERING_NOTHING,
      firstDragEvent: false,
      hoveredZone: -1,
    };
  } else if (intersectingZoneIndex !== state.hoveredZone) {
    return {
      ...state,
      firstDragEvent: false,
      hoveredZone: intersectingZoneIndex,
    };
  }
  return state;
};

const resizingBottom = (state: PageState, action: MouseMoveAction): PageState => {
  if (!state.previousMouseLocation) {
    return state;
  }
  const activeZone = state.zones[state.activeZones[0]];
  const updatedZone = {
    ...activeZone,
    maxY: Math.min(
      action.imageSize.height,
      activeZone.maxY - (state.previousMouseLocation.y - action.location.y),
    ),
  };
  if (updatedZone.maxY - updatedZone.minY < 10) {
    return { ...state, previousMouseLocation: action.location };
  }
  // check for intersection
  const overlapFunction = doZonesOverlap(updatedZone);
  for (let i = 0; i < state.zones.length; i++) {
    if (i === state.activeZones[0]) {
      continue;
    }
    if (overlapFunction(state.zones[i])) {
      return {
        ...state,
        previousMouseLocation: action.location,
      };
    }
  }
  const updatedZones = [
    ...state.zones.slice(0, state.activeZones[0]),
    updatedZone,
    ...state.zones.slice(state.activeZones[0] + 1),
  ];
  return {
    ...state,
    firstDragEvent: false,
    pageSaveState: PageSaveState.Dirty,
    previousMouseLocation: action.location,
    zones: state.firstDragEvent
      ? state.zoneHistory.push(updatedZones)
      : state.zoneHistory.replace(updatedZones),
  };
};

