const mouseMovedOnPage = (state: PageState, action: MouseMoveAction): PageState => {
  switch (state.currentAction) {
    case CurrentAction.CREATING_ZONE:
    case CurrentAction.SELECTING:
      return creatingOrSelectingZones(state, action);
    case CurrentAction.MOVING:
      return movingZones(state, action);
    case CurrentAction.HOVERING_NOTHING:
      return hoveringNothing(state, action);
    case CurrentAction.HOVERING_ZONE:
      return hoveringZone(state, action);
    case CurrentAction.RESIZING_BOTTOM:
      return resizingBottom(state, action);
    case CurrentAction.RESIZING_LEFT:
      return resizingLeft(state, action);
    case CurrentAction.RESIZING_RIGHT:
      return resizingRight(state, action);
    case CurrentAction.RESIZING_TOP:
      return resizingTop(state, action);
  }
};

export const mouseMove = (state: State, action: MouseMoveAction): State => {
  const updatedPage = mouseMovedOnPage(state.pages[state.activePage], action);
  if (updatedPage === state.pages[state.activePage]) {
    return state;
  }
  let currentLabel = state.currentLabel;
  // check if the active zones changed
  if (
    updatedPage.activeZones !== state.pages[state.activePage].activeZones &&
    updatedPage.activeZones.length > 0
  ) {
    const uniqueLabels: { [key: string]: true } = {};
    for (const zoneIndex of updatedPage.activeZones) {
      uniqueLabels[updatedPage.zones[zoneIndex].label] = true;
    }
    if (Object.keys(uniqueLabels).length === 1) {
      currentLabel = Object.keys(uniqueLabels)[0];
    }
  }
  return {
    ...state,
    currentLabel,
    pages: [
      ...state.pages.slice(0, state.activePage),
      updatedPage,
      ...state.pages.slice(state.activePage + 1),
    ],
  };
};
