import React, { useState } from "react";

import { Dispatch } from "../StateManager/state";
import { Filters } from "./Filters";
import { PageList } from "./PageList";

type Props = Readonly<{
  activePage: number;
  dispatch: Dispatch;
  highConfidenceThreshold: number;
  pageConfidences: Array<number>;
  pageImageURIs: ReadonlyArray<string>;
}>;

export const PagePreview = ({
  activePage,
  dispatch,
  highConfidenceThreshold,
  pageConfidences,
  pageImageURIs,
}: Props) => {
  const [threshold, setThreshold] = useState(highConfidenceThreshold);
  const [showAll, setShowAll] = useState(false);
  return (
    <div className="page-preview">
      <Filters
        resetThreshold={() => {
          setThreshold(highConfidenceThreshold);
        }}
        setThreshold={setThreshold}
        showAll={showAll}
        threshold={threshold}
        toggleShowAll={() => {
          setShowAll(!showAll);
        }}
      />
      <PageList
        activePage={activePage}
        dispatch={dispatch}
        highConfidenceThreshold={threshold}
        pageConfidences={pageConfidences}
        pageImageURIs={pageImageURIs}
        showAll={showAll}
      />
    </div>
  );
};
