import React from "react";
import { Col, ListGroup, ListGroupItem, Row } from "reactstrap";

import { titalize } from "@common/lib/titalize";
import { Dispatch, State, TaxonomyMode } from "../StateManager/state";
import { ColorSwatch } from "./ColorSwatch";

type Props = Readonly<{
  activeLabel: State["currentLabel"];
  currentLabelGroup: State["currentLabelGroup"];
  dispatch: Dispatch;
  readonly: boolean;
  taxonomy: State["taxonomy"];
}>;

export const LabelList = ({
  activeLabel,
  currentLabelGroup,
  dispatch,
  readonly,
  taxonomy,
}: Props) => {
  const showHeaders = taxonomy.mode === TaxonomyMode.Complex;
  return (
    <div className="label-list">
      <Row>
        <Col>
          <ListGroup className={showHeaders ? "indented" : ""}>
            {Object.keys(taxonomy.orderedLabels).map((key, groupIndex) => {
              const labelList: Array<string> | undefined =
                taxonomy.orderedLabels[key as keyof State["taxonomy"]["orderedLabels"]];
              if (!labelList) {
                return null;
              }
              const labelGroupClasses = ["label-group"];
              if (currentLabelGroup === key) {
                labelGroupClasses.push("active-label-group");
              }
              return (
                <div className={labelGroupClasses.join(" ")} key={key}>
                  {showHeaders && (
                    <ListGroupItem className="header">{titalize(key)} </ListGroupItem>
                  )}
                  {labelList.map((label, labelIndex) => {
                    let hotKey = "";
                    if (groupIndex < 9 && labelIndex < 9) {
                      if (showHeaders) {
                        hotKey = `${groupIndex + 1} + ${labelIndex + 1}`;
                      } else {
                        hotKey = `${groupIndex + 1}`;
                      }
                    }
                    const title = titalize(label);
                    return (
                      <ListGroupItem
                        active={activeLabel === label}
                        className={readonly ? "" : "selectable"}
                        key={label}
                        onClick={() => {
                          if (!readonly) {
                            dispatch({
                              label,
                              type: "CHANGE CURRENT LABEL",
                            });
                          }
                        }}
                        title={title}
                      >
                        <div className="align-items-center d-flex text-truncate">
                          <ColorSwatch color={taxonomy.rawLabels[label].color} />
                          {title}
                        </div>
                        {hotKey && !readonly && <kbd>{hotKey}</kbd>}
                      </ListGroupItem>
                    );
                  })}
                </div>
              );
            })}
          </ListGroup>
        </Col>
      </Row>
    </div>
  );
};
LabelList.displayName = "LabelList";
