import { PageSaveState, PageState, State, TaxonomyMode, ZoomLevel } from "../state";
import { changeCurrentLabelOnPage } from "./changeCurrentLabel";

export interface KeyPressedAction {
  ctrlKey: boolean;
  shiftKey: boolean;
  key: string;
  keyCode: number;
  onSave: () => void;
  preventDefault: () => void;
  type: "KEY PRESSED";
}

const keyPressedOnPage = (state: PageState, action: KeyPressedAction): PageState => {
  switch (action.key) {
    case "backspace":
    case "delete":
    case "d": {
      action.preventDefault();
      // delete the active zones
      if (state.activeZones.length < 1) {
        return state;
      }
      return {
        ...state,
        activeZones: [],
        pageSaveState: PageSaveState.Dirty,
        zones: state.zoneHistory.push(
          state.zones.filter((_, i) => state.activeZones.indexOf(i) < 0),
        ),
      };
    }
    case "y":
    case "z": {
      if (action.ctrlKey) {
        action.preventDefault();
        if ((action.key === "z" && action.shiftKey) || action.key === "y") {
          // redo action is Ctrl+Y or Shift+Ctrl+Z
          const nextZones = state.zoneHistory.redo();
          if (nextZones !== state.zones) {
            return state;
          }
          return {
            ...state,
            activeZones: [],
            pageSaveState: PageSaveState.Dirty,
            zones: nextZones,
          };
        }
        // undo action is Ctrl+Z
        const previousZones = state.zoneHistory.undo();
        if (previousZones === state.zones) {
          return state;
        }
        return {
          ...state,
          activeZones: [],
          pageSaveState: PageSaveState.Dirty,
          zones: previousZones,
        };
      }
      break;
    }
    case "a":
      if (action.ctrlKey) {
        action.preventDefault();
        const activeZones = [];
        for (let i = 0; i < state.zones.length; i++) {
          activeZones.push(i);
        }
        return {
          ...state,
          activeZones,
        };
      }
      break;
    case "s":
      if (action.ctrlKey) {
        action.preventDefault();
        if (state.pageSaveState !== PageSaveState.Dirty) {
          return state;
        }
        setTimeout(() => {
          action.onSave();
        }, 0);
      }
      break;
  }
  return state;
};

// tslint:disable-next-line: cyclomatic-complexity
export const keyPressed = (state: State, action: KeyPressedAction): State => {
  const asNumber = parseInt(action.key, 10);
  if (!isNaN(asNumber) && asNumber > 0) {
    action.preventDefault();
    const index = asNumber - 1;
    if (state.currentLabelGroup === "") {
      if (index < Object.keys(state.taxonomy.orderedLabels).length) {
        if (state.taxonomy.mode === TaxonomyMode.Complex) {
          return {
            ...state,
            currentLabelGroup: Object.keys(state.taxonomy.orderedLabels)[
              index
            ] as keyof State["taxonomy"]["orderedLabels"],
          };
        } else {
          const selectedKey = Object.keys(state.taxonomy.orderedLabels)[
            index
          ] as keyof State["taxonomy"]["orderedLabels"];
          const selectedList = state.taxonomy.orderedLabels[selectedKey];
          if (Array.isArray(selectedList) && selectedList.length > 0) {
            const updatedChangeLabelPage = changeCurrentLabelOnPage(state.pages[state.activePage], {
              label: selectedList[0],
              type: "CHANGE CURRENT LABEL",
            });
            let changeLabelPages = state.pages;
            if (updatedChangeLabelPage !== state.pages[state.activePage]) {
              changeLabelPages = [
                ...changeLabelPages.slice(0, state.activePage),
                updatedChangeLabelPage,
                ...changeLabelPages.slice(state.activePage + 1),
              ];
            }
            return {
              ...state,
              currentLabel: selectedList[0],
              pages: changeLabelPages,
            };
          }
        }
      }
      return state;
    }
    const labelList = state.taxonomy.orderedLabels[state.currentLabelGroup];
    if (Array.isArray(labelList) && index < labelList.length) {
      const updatedChangeLabelPage = changeCurrentLabelOnPage(state.pages[state.activePage], {
        label: labelList[index],
        type: "CHANGE CURRENT LABEL",
      });
      let changeLabelPages = state.pages;
      if (updatedChangeLabelPage !== state.pages[state.activePage]) {
        changeLabelPages = [
          ...changeLabelPages.slice(0, state.activePage),
          updatedChangeLabelPage,
          ...changeLabelPages.slice(state.activePage + 1),
        ];
      }
      return {
        ...state,
        currentLabel: labelList[index],
        currentLabelGroup: "",
        pages: changeLabelPages,
      };
    }
    return {
      ...state,
      currentLabelGroup: "",
    };
  }
  if (action.key === "+") {
    action.preventDefault();
    let newZoomLevel = state.zoom;
    switch (state.zoom) {
      case ZoomLevel["1x"]:
        newZoomLevel = ZoomLevel["1.5x"];
        break;
      case ZoomLevel["1.5x"]:
        newZoomLevel = ZoomLevel["2x"];
        break;
      case ZoomLevel["2x"]:
        newZoomLevel = ZoomLevel["2.5x"];
        break;
      case ZoomLevel["2.5x"]:
        newZoomLevel = ZoomLevel["3x"];
        break;
    }
    return {
      ...state,
      zoom: newZoomLevel,
    };
  } else if (action.key === "-") {
    action.preventDefault();
    let newZoomLevel = state.zoom;
    switch (state.zoom) {
      case ZoomLevel["1.5x"]:
        newZoomLevel = ZoomLevel["1x"];
        break;
      case ZoomLevel["2x"]:
        newZoomLevel = ZoomLevel["1.5x"];
        break;
      case ZoomLevel["2.5x"]:
        newZoomLevel = ZoomLevel["2x"];
        break;
      case ZoomLevel["3x"]:
        newZoomLevel = ZoomLevel["2.5x"];
        break;
    }
    return {
      ...state,
      zoom: newZoomLevel,
    };
  }
  const updatedPage = keyPressedOnPage(state.pages[state.activePage], action);
  let pages = state.pages;
  if (updatedPage !== state.pages[state.activePage]) {
    pages = [
      ...state.pages.slice(0, state.activePage),
      updatedPage,
      ...state.pages.slice(state.activePage + 1),
    ];
  }
  return {
    ...state,
    currentLabelGroup: "",
    pages,
  };
};
