import React, { RefObject, useMemo, useRef } from "react";
import ReactResizeDetector from "react-resize-detector";

import { Dispatch, State } from "../StateManager/state";
import { JobState, ZoningTaxonomy } from "../types";
import { Canvas } from "./Canvas";
import { centerOnZoom } from "./centerOnZoom";

type Props = Readonly<{
  autoShrink: boolean;
  dispatch: Dispatch;
  jobState: JobState;
  onSave: () => void;
  pageImageURIs: Array<string>;
  readonly: boolean;
  state: State;
  taxonomy: ZoningTaxonomy;
}>;
interface IResizeDetectorCallbackArgs {
  height: number;
  width: number;
}
export const Renderer = ({
  autoShrink,
  dispatch,
  jobState,
  onSave,
  pageImageURIs,
  readonly,
  state,
  taxonomy,
}: Props) => {
  const scrollRef: RefObject<HTMLDivElement> = useRef(null);
  // if the user changes the zoom, we recentre the canvas on the screen
  centerOnZoom(scrollRef, state.zoom);
  const disableCanvas = useMemo((): boolean => {
    if (readonly) {
      return true;
    }
    switch (jobState) {
      case JobState.Default:
        return false;
      case JobState.Saving:
      case JobState.Submitting:
        return true;
    }
  }, [jobState, readonly]);
  return (
    <div className="renderer">
      <ReactResizeDetector handleHeight handleWidth>
        {({ height, width }: IResizeDetectorCallbackArgs) => (
          <div
            style={{
              height: `${height}px`,
              width: `${width}px`,
            }}
          >
            <div
              ref={scrollRef}
              style={{
                height: "100%",
                overflow: "auto",
                position: "relative",
                width: "100%",
              }}
            >
              <Canvas
                autoShrink={autoShrink}
                disabled={disableCanvas}
                dispatch={dispatch}
                height={height}
                imageURI={pageImageURIs[state.activePage]}
                onSave={onSave}
                parentScrollRef={scrollRef.current}
                state={state}
                taxonomy={taxonomy}
                width={width}
                zoom={state.zoom}
              />
            </div>
          </div>
        )}
      </ReactResizeDetector>
    </div>
  );
};
