import { Color } from "./helpers";

export type URI = string;

export interface IZone {
  confidence: number;
  humanGenerated: boolean;
  label: string;
  maxX: number;
  maxY: number;
  minX: number;
  minY: number;
}

export interface IPage {
  confidence?: number;
  height?: number;
  index: number;
  width?: number;
  zones: Array<IZone>;
}

export interface IApiZone {
  box: Array<number>;
  class: string;
  scores: number;
}

export interface IApiPage {
  page_confidence: number;
  predictions: Array<IApiZone>;
}

interface IZoningPageMetadata {
  images: {
    height: number;
    width: number;
  };
}

export interface IZoningMetadata {
  pages: Array<IZoningPageMetadata>;
}

export interface ZoningLabel {
  color: Color;
  group: string;
}
export interface ZoningTaxonomy {
  [child: string]: ZoningLabel;
}
