import { State, ZoomLevel } from "../state";

export interface ChangeZoomLevelAction {
  type: "CHANGE ZOOM LEVEL";
  zoom: ZoomLevel;
}

export const changeZoomLevel = (state: State, action: ChangeZoomLevelAction): State => {
  if (action.zoom === state.zoom) {
    return state;
  }
  return {
    ...state,
    zoom: action.zoom,
  };
};
