import { Color } from "../Zoning/helpers";
import { DEFAULT_TAXONOMY, DefaultTaxonomyEntries, ZoningTaxonomy } from "../Zoning/types";

export const addTaxonomyColorsAndHotkeys = (raw: any): ZoningTaxonomy => {
  if (typeof raw === "object" && raw !== null && !Array.isArray(raw)) {
    const toReturn: ZoningTaxonomy = {};
    const labels = Object.keys(raw);
    for (const label of labels) {
      let color = new Color(0, 0, 0);
      if (label in DefaultTaxonomyEntries) {
        color = DefaultTaxonomyEntries[label].color;
      } else if (typeof raw[label] === "string" && raw[label] in DefaultTaxonomyEntries) {
        color = DefaultTaxonomyEntries[raw[label]].color;
      }
      if (typeof raw[label] === "string") {
        toReturn[label] = {
          color,
          group: raw[label],
        };
      } else if (
        typeof raw[label] === "object" &&
        raw[label] !== null &&
        !Array.isArray(raw[label])
      ) {
        toReturn[label] = {
          color:
            typeof raw[label].color === "string"
              ? Color.fromString(raw[label].color)
              : new Color(0, 0, 0),
          group: typeof raw[label].group === "string" ? raw[label].group : "",
        };
      } else {
        throw new Error("Invalid taxonomy format.");
      }
    }
    return toReturn;
  }
  return DEFAULT_TAXONOMY;
};
