import React from "react";
import { Rect } from "react-konva";

import { Color } from "../helpers";
import { ZoningTaxonomy } from "../types";
import { Point, Size } from "./types";

type Props = Readonly<{
  label: string;
  point: Point;
  size: Size;
  taxonomy: ZoningTaxonomy;
}>;

const UnknownFill = Color.fromString("rgba(0.0, 0.0, 0.0, 0.2)");
const UnknownStroke = Color.fromString("rgba(0.0, 0.0, 0.0, 0.5)");

export const Zone = ({ label, point, size, taxonomy }: Props) => {
  let fillColor = UnknownFill;
  let strokeColor = UnknownStroke;
  if (label in taxonomy && taxonomy[label].color) {
    fillColor = taxonomy[label].color.copy();
    fillColor.alpha = 0.2;
    strokeColor = taxonomy[label].color.copy();
    strokeColor.alpha = 0.5;
  }
  return (
    <Rect
      fill={fillColor.toRGBAString()}
      stroke={strokeColor.toRGBAString()}
      {...point}
      {...size}
      strokeWidth={1.0}
      opacity={1.0}
    />
  );
};
Zone.displayName = "Zone";
