import React from "react";
import { Layer } from "react-konva";

import { getResizeHandlerPositionsForZone } from "../helpers";
import { CurrentAction } from "../StateManager/state";
import { IZone, ZoningTaxonomy } from "../types";
import { Offset, Scale } from "./types";
import { Zone } from "./Zone";
import { ZoneResizeControl } from "./ZoneResizeControl";
import { ZoneTag } from "./ZoneTag";

type Props = Readonly<{
  activeZones: Array<number>;
  currentAction: CurrentAction;
  hoveredZone: number;
  offset: Offset;
  scale: Scale;
  taxonomy: ZoningTaxonomy;
  zones: Array<IZone>;
}>;

const positions = ["top", "right", "bottom", "left"];

export const Zones = ({
  activeZones,
  currentAction,
  hoveredZone,
  offset,
  scale,
  taxonomy,
  zones,
}: Props) => {
  if (isNaN(offset.x) || isNaN(offset.y) || isNaN(scale.x) || isNaN(scale.y)) {
    return <Layer />;
  }
  const showResizeHandlers = !(
    currentAction === CurrentAction.RESIZING_BOTTOM ||
    currentAction === CurrentAction.RESIZING_LEFT ||
    currentAction === CurrentAction.RESIZING_RIGHT ||
    currentAction === CurrentAction.RESIZING_TOP
  );
  return (
    <Layer>
      {zones.map((z, index) => (
        <Zone
          key={`zone-${index}`}
          label={z.label}
          point={{
            x: z.minX * scale.x + offset.x,
            y: z.minY * scale.y + offset.y,
          }}
          size={{
            height: (z.maxY - z.minY) * scale.y,
            width: (z.maxX - z.minX) * scale.x,
          }}
          taxonomy={taxonomy}
        />
      ))}
      {showResizeHandlers &&
        activeZones.map(z =>
          getResizeHandlerPositionsForZone(zones[z]).map((center, position) => (
            <ZoneResizeControl
              center={{
                x: center.x * scale.x + offset.x,
                y: center.y * scale.y + offset.y,
              }}
              key={`resize-${z}-${positions[position]}`}
            />
          )),
        )}
      <ZoneTag hoveredZone={hoveredZone} offset={offset} scale={scale} zones={zones} />
    </Layer>
  );
};
