import { useCallback, useEffect } from "react";

import { Dispatch } from "../StateManager/state";

type Props = Readonly<{
  dispatch: Dispatch;
  onSave: () => void;
}>;
export const KeyboardHandler = ({ dispatch, onSave }: Props) => {
  const onKeyDown = useCallback(
    (e: KeyboardEvent) => {
      const key = e.key.toLowerCase();
      const { ctrlKey, keyCode, shiftKey } = e;
      if (key === "s" && ctrlKey) {
        e.preventDefault();
        onSave();
        return;
      }
      dispatch({
        ctrlKey,
        key,
        keyCode,
        onSave,
        preventDefault: () => {
          e.preventDefault();
        },
        shiftKey,
        type: "KEY PRESSED",
      });
    },
    [dispatch, onSave],
  );
  useEffect(() => {
    window.addEventListener("keydown", onKeyDown);
    return () => {
      window.removeEventListener("keydown", onKeyDown);
    };
  }, [onKeyDown]);
  return null;
};
