import React, { useEffect, useMemo } from "react";
import { Stage } from "react-konva";

import { useImage } from "@common/util/useImage";
import { Dispatch, State, ZoomLevel } from "../StateManager/state";
import { ZoningTaxonomy } from "../types";
import { KeyboardHandler } from "./KeyboardHandler";
import { MouseHandler } from "./MouseHandler";
import { PageImage } from "./PageImage";
import { Selection } from "./Selection";
import { usePageDimensions } from "./usePageDimensions";
import { Zones } from "./Zones";

type Props = Readonly<{
  autoShrink: boolean;
  disabled: boolean;
  dispatch: Dispatch;
  height: number;
  imageURI: string;
  onSave: () => void;
  parentScrollRef: HTMLElement | null;
  state: State;
  taxonomy: ZoningTaxonomy;
  width: number;
  zoom: ZoomLevel;
}>;

export const Canvas = ({
  autoShrink,
  disabled,
  dispatch,
  height,
  imageURI,
  onSave,
  parentScrollRef,
  state,
  taxonomy,
  width,
  zoom,
}: Props) => {
  const page = state.pages[state.activePage];
  const { activeZones, currentAction, dimensions, hoveredZone, selection } = page;
  const containerSize = useMemo(() => ({ height: height * zoom, width: width * zoom }), [
    height,
    width,
    zoom,
  ]);
  const [imageSize, offset, scale] = usePageDimensions(containerSize, dimensions);
  const [image, status] = useImage(imageURI, true, true);
  useEffect(() => {
    if (!parentScrollRef || status !== "loaded" || image?.src !== imageURI) {
      return;
    }
    parentScrollRef.scrollTo((parentScrollRef.scrollWidth - parentScrollRef.clientWidth) / 2, 0);
  }, [imageURI, status]);
  if (isNaN(containerSize.height) || isNaN(containerSize.width)) {
    return null;
  } else if (image === undefined || status !== "loaded" || image.src !== imageURI) {
    const loadingStyles = {
      alignItems: "center",
      backgroundColor: "white",
      display: "flex",
      height: `${Math.floor(imageSize.height / zoom)}px`,
      justifyContent: "center",
      margin: "auto",
      width: `${Math.floor(imageSize.width / zoom)}px`,
    };
    const spinnerStyles = {
      height: "5rem",
      width: "5rem",
    };
    return (
      <div style={loadingStyles}>
        <div>
          <div className="ml-auto mr-auto spinner-border" role="status" style={spinnerStyles}>
            <span className="sr-only">Loading...</span>
          </div>
          <div className="text-muted mt-3">Loading &hellip;</div>
        </div>
      </div>
    );
  }
  return (
    <Stage {...containerSize}>
      <PageImage containerSize={containerSize} image={image} offset={offset} size={imageSize} />
      <Zones
        activeZones={activeZones}
        currentAction={currentAction}
        hoveredZone={hoveredZone}
        offset={offset}
        scale={scale}
        taxonomy={taxonomy}
        zones={page.zones}
      />
      <Selection offset={offset} scale={scale} selection={selection} />
      <MouseHandler
        autoShrink={autoShrink}
        disableActions={disabled}
        dispatch={dispatch}
        offset={offset}
        scale={scale}
        size={imageSize}
        state={state}
      />
      {!disabled && <KeyboardHandler dispatch={dispatch} onSave={onSave} />}
    </Stage>
  );
};
