import React, { ReactNode, useEffect, useState } from "react";
import { SSEProvider } from "react-hooks-sse";
import { Nav, NavItem, NavLink, TabContent, TabPane } from "reactstrap";

import { Actions, LoggedInState, State } from "../../types";
import { JobList } from "./JobList";
import { Settings } from "./Settings";

enum ActiveTab {
  jobs = "jobs",
  settings = "settings",
}

type Props = Readonly<{
  actions: Actions;
  auth: LoggedInState;
  blockingStep: string;
  customSettings: ReactNode | null;
  jobs: State["jobs"];
  jobState: State["job"];
}>;

export const Authenticated = ({
  actions,
  auth,
  blockingStep,
  customSettings,
  jobs,
  jobState,
}: Props) => {
  const [activeTab, setActiveTab] = useState<ActiveTab>(ActiveTab.jobs);
  useEffect(() => {
    setActiveTab(ActiveTab.jobs);
  }, [auth.activeKey]);
  const { token, user } = auth;
  const eventStreamEndPoint = `https://api.innodata.com/v1.1/users/${encodeURIComponent(
    user.id,
  )}/events?token=${encodeURIComponent(token)}`;
  return (
    <div className="d-flex flex-column h-100 p-3">
      <Nav tabs>
        <NavItem>
          <NavLink
            className={activeTab === ActiveTab.jobs ? "active" : ""}
            onClick={() => {
              setActiveTab(ActiveTab.jobs);
            }}
          >
            Jobs
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={activeTab === ActiveTab.settings ? "active" : ""}
            onClick={() => {
              setActiveTab(ActiveTab.settings);
            }}
          >
            <i aria-label="Settings" className="fa fa-cogs" />
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={activeTab} className="flex-fill overflow-hidden">
        <TabPane
          tabId={ActiveTab.jobs}
          className="border-bottom border-left border-right h-100 overflow-hidden p-2"
        >
          <SSEProvider endpoint={eventStreamEndPoint}>
            <JobList
              actions={actions}
              auth={auth}
              blockingStep={blockingStep}
              jobs={jobs}
              jobState={jobState}
            />
          </SSEProvider>
        </TabPane>
        <TabPane
          tabId={ActiveTab.settings}
          className="border-bottom border-left border-right h-100 overflow-hidden p-2 "
          style={{ overflowX: "hidden", overflowY: "auto" }}
        >
          <Settings actions={actions} auth={auth} customSettings={customSettings} />
        </TabPane>
      </TabContent>
    </div>
  );
};
Authenticated.displayName = "Authenticated";
